import React, { useState } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";

const AddSubject = () => {
  let history = useHistory();
  const [subject, setSubject] = useState({
    subjectId: "",
    subjectName: "",
    subjectCredit: "",
    subjectBranch: "",
    subjectSemester: "",
  });

  const {
    subjectId,
    subjectName,
    subjectCredit,
    subjectBranch,
    subjectSemester,
  } = subject;
  const onInputChange = (e) => {
    setSubject({ ...subject, [e.target.name]: e.target.value });
  };

  const config = {
    headers:{
      Authorization : 'Bearer ' + localStorage.getItem('token')
    }
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    await axios.post("http://localhost:8080/admin/subject", subject, config);
    console.log(subject);
    history.push("/subjects");
  };
  return (
    <div className="container">
      <div className="w-75 mx-auto shadow p-5">
        <h2 className="text-center mb-4">Add A Subject</h2>
        <form onSubmit={(e) => onSubmit(e)}>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter subjectId"
              name="subjectId"
              value={subjectId}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter subjectName"
              name="subjectName"
              value={subjectName}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter subject Credit"
              name="subjectCredit"
              value={subjectCredit}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter subject branch"
              name="subjectBranch"
              value={subjectBranch}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter subject semester"
              name="subjectSemester"
              value={subjectSemester}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <button className="btn btn-primary btn-block">Add Subject</button>
        </form>
      </div>
    </div>
  );
};

export default AddSubject;
