import React, { useState } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";

const Login = () => {
  let history = useHistory();
  const [credential, setCredential] = useState({
    username: "",
    password: "",
  });

  const { username, password } = credential;

  const onInputChange = (e) => {
    setCredential({ ...credential, [e.target.name]: e.target.value });
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    await axios
      .post("http://localhost:8080/api/auth/signin", credential)
      .then((res) => {
        console.log(res);
        localStorage.setItem("token", res.data.jwttoken);
        localStorage.setItem("id", res.data.username);
        localStorage.setItem("role", res.data.role);
        // history.push(`/results/${result.studentId}`);
      })
      .catch((err) => {
        console.log(err);
      });

    if (localStorage.getItem("role") == "ROLE_ADMIN") {
      history.push(`/admin`);
    } else if (localStorage.getItem("role") == "ROLE_TEACHER") {
      history.push(`/teacher`);
    } else if (localStorage.getItem("role") == "ROLE_STUDENT") {
      history.push(`/student`);
    }
  };

  return (
    <div class="container">
      <div className="w-50 mx-auto mt-5 shadow p-5">
        <form onSubmit={(e) => onSubmit(e)} class="form-signin">
          <div class="text-center">
            <img
              class="mb-5 "
              src="mortarboard (1).png"
              alt=""
              width="100"
              height="100"
            />
          </div>

          <h1 class="h3 mb-3 text-center font-weight-normal">Please Login</h1>

          <input
            type="text"
            class="form-control form-control-lg mb-2"
            placeholder="User Id"
            name="username"
            value={username}
            onChange={(e) => onInputChange(e)}
            required
            autofocus
          />

          <input
            type="password"
            class="form-control form-control-lg mb-2"
            placeholder="Password"
            name="password"
            value={password}
            onChange={(e) => onInputChange(e)}
            required
          />

          <button class="btn btn-lg btn-primary btn-block" type="submit">
            Log in
          </button>
        </form>
      </div>
    </div>
  );
};

export default Login;
