import React, { useState, useEffect } from "react";
import "./profile.css";
import axios from "axios";

const StudentProfile = () => {
  const [student, setStudent] = useState({
    studentId: "",
    studentName: "",
    studentEmail: "",
    studentGender: "",
    studentDob: "",
    studentPhone: "",
    studentAddress: "",
    studentNumber: "",
    studentClass: "",
    studentBranch: "",
  });
  //   const { id } = localStorage.getItem("id");

  useEffect(() => {
    loadStudent();
  }, []);

  const config = {
    headers: {
      Authorization: "Bearer " + localStorage.getItem("token"),
    },
  };

  const loadStudent = async () => {
    const res = await axios.get(
      `http://localhost:8080/admin/student/${localStorage.getItem("id")}`,
      config
    );
    setStudent(res.data);
    // console.log(res.data);
  };
  return (
    <div className="container py-4">
      <h1 className="display-5">Your Id: {localStorage.getItem("id")}</h1>
      <hr />
      <div class="row">
        <div class="col-md-4">Name:</div>
        <div class="col-md-6">{student.studentName}</div>
      </div>
      <div class="row">
        <div class="col-md-4">Email:</div>
        <div class="col-md-6">{student.studentEmail}</div>
      </div>
      <div class="row">
        <div class="col-md-4">Gender:</div>
        <div class="col-md-6">{student.studentGender}</div>
      </div>
      <div class="row">
        <div class="col-md-4">Dob:</div>
        <div class="col-md-6">{student.studentDob}</div>
      </div>
      <div class="row">
        <div class="col-md-4">Contact no.:</div>
        <div class="col-md-6">{student.studentPhone}</div>
      </div>
      <div class="row">
        <div class="col-md-4">Address:</div>
        <div class="col-md-6">{student.studentAddress}</div>
      </div>
      <div class="row">
        <div class="col-md-4">Roll No:</div>
        <div class="col-md-6">{student.studentNumber}</div>
      </div>
      <div class="row">
        <div class="col-md-4">Class:</div>
        <div class="col-md-6">{student.studentClass}</div>
      </div>
      <div class="row">
        <div class="col-md-4">Branch:</div>
        <div class="col-md-6">{student.studentBranch}</div>
      </div>
    </div>
  );
};

export default StudentProfile;
