import React from "react";
import "../../../node_modules/bootstrap/dist/css/bootstrap.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import AddResult from "./ManageExamResult/AddResult";
import ListStudent from "./ManageExamResult/ListStudent";
import EditResult from "./ManageExamResult/EditResult";
import ListResult from "./ManageExamResult/ListResult";

function RouteExamResult() {
  return (
    <div className="App">
      <Router>
        <div className="App">
          <Switch>
            <Route exact path="/results" component={ListStudent} />
            <Route exact path="/results/add" component={AddResult} />
            <Route exact path="/results/edit/:id" component={EditResult} />
            <Route exact path="/results/:id" component={ListResult} />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default RouteExamResult;
