import React, { useState } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";

const AddStudent = () => {
  let history = useHistory();
  const [student, setStudent] = useState({
    studentId: "",
    studentName: "",
    studentEmail: "",
    studentGender: "",
    studentDob: "",
    studentPhone: "",
    studentAddress: "",
    studentNumber: "",
    studentClass: "",
    studentBranch: "",
  });

  const {
    studentId,
    studentName,
    studentEmail,
    studentGender,
    studentDob,
    studentPhone,
    studentAddress,
    studentNumber,
    studentClass,
    studentBranch,
  } = student;
  const onInputChange = (e) => {
    setStudent({ ...student, [e.target.name]: e.target.value });
  };

  const config = {
    headers:{
      Authorization : 'Bearer ' + localStorage.getItem('token')
    }
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    await axios.post("http://localhost:8080/admin/student", student, config);
    console.log(student);
    history.push("/students");
  };
  return (
    <div className="container">
      <div className="w-75 mx-auto shadow p-5">
        <h2 className="text-center mb-4">Add A Student</h2>
        <form onSubmit={(e) => onSubmit(e)}>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter studentId"
              name="studentId"
              value={studentId}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter studentName"
              name="studentName"
              value={studentName}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="email"
              className="form-control form-control-lg"
              placeholder="Enter E-mail Address"
              name="studentEmail"
              value={studentEmail}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter Student Gender"
              name="studentGender"
              value={studentGender}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter DOB"
              name="studentDob"
              value={studentDob}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter Contact No."
              name="studentPhone"
              value={studentPhone}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter Address"
              name="studentAddress"
              value={studentAddress}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter Roll No"
              name="studentNumber"
              value={studentNumber}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter Class Name"
              name="studentClass"
              value={studentClass}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter Branch Name"
              name="studentBranch"
              value={studentBranch}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <button className="btn btn-primary btn-block">Add Student</button>
        </form>
      </div>
    </div>
  );
};

export default AddStudent;
