import React, { useState, useEffect } from "react";
import "./profile.css";
import axios from "axios";

const TeacherProfile = () => {
  const [teacher, setTeacher] = useState({
    teacherId: "",
    teacherName: "",
    teacherEmail: "",
    teacherGender: "",
    teacherDob: "",
    teacherPhone: "",
    teacherAddress: "",
  });
  //   const { id } = localStorage.getItem("id");

  useEffect(() => {
    loadTeacher();
  }, []);

  const config = {
    headers: {
      Authorization: "Bearer " + localStorage.getItem("token"),
    },
  };

  const loadTeacher = async () => {
    const res = await axios.get(
      `http://localhost:8080/admin/teacher/${localStorage.getItem("id")}`,
      config
    );
    setTeacher(res.data);
    // console.log(res.data);
  };
  return (
    <div className="container py-4">
      <h1 className="display-5">Your Id: {localStorage.getItem("id")}</h1>
      <hr />
      <div class="row">
        <div class="col-md-4">Name:</div>
        <div class="col-md-6">{teacher.teacherName}</div>
      </div>
      <div class="row">
        <div class="col-md-4">Email:</div>
        <div class="col-md-6">{teacher.teacherEmail}</div>
      </div>
      <div class="row">
        <div class="col-md-4">Gender:</div>
        <div class="col-md-6">{teacher.teacherGender}</div>
      </div>
      <div class="row">
        <div class="col-md-4">Dob:</div>
        <div class="col-md-6">{teacher.teacherDob}</div>
      </div>
      <div class="row">
        <div class="col-md-4">Contact no.:</div>
        <div class="col-md-6">{teacher.teacherPhone}</div>
      </div>
      <div class="row">
        <div class="col-md-4">Address:</div>
        <div class="col-md-6">{teacher.teacherAddress}</div>
      </div>
    </div>
  );
};

export default TeacherProfile;
