import React, { useState } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";

const AddTeacher = () => {
  let history = useHistory();
  const [teacher, setTeacher] = useState({
    teacherId: "",
    teacherName: "",
    teacherEmail: "",
    teacherGender: "",
    teacherDob: "",
    teacherPhone: "",
    teacherAddress: "",
  });

  const {
    teacherId,
    teacherName,
    teacherEmail,
    teacherGender,
    teacherDob,
    teacherPhone,
    teacherAddress,
  } = teacher;
  const onInputChange = (e) => {
    setTeacher({ ...teacher, [e.target.name]: e.target.value });
  };

  const config = {
    headers:{
      Authorization : 'Bearer ' + localStorage.getItem('token')
    }
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    await axios.post("http://localhost:8080/admin/teacher", teacher, config);
    console.log(teacher);
    history.push("/teachers");
  };
  return (
    <div className="container">
      <div className="w-75 mx-auto shadow p-5">
        <h2 className="text-center mb-4">Add A Teacher</h2>
        <form onSubmit={(e) => onSubmit(e)}>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter teacherId"
              name="teacherId"
              value={teacherId}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter teacherName"
              name="teacherName"
              value={teacherName}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="email"
              className="form-control form-control-lg"
              placeholder="Enter E-mail Address"
              name="teacherEmail"
              value={teacherEmail}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter Teacher Gender"
              name="teacherGender"
              value={teacherGender}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter DOB"
              name="teacherDob"
              value={teacherDob}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter Contact No."
              name="teacherPhone"
              value={teacherPhone}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter Address"
              name="teacherAddress"
              value={teacherAddress}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <button className="btn btn-primary btn-block">Add Teacher</button>
        </form>
      </div>
    </div>
  );
};

export default AddTeacher;
