import React from "react";
import "../../../node_modules/bootstrap/dist/css/bootstrap.css";
import RouteExamResult from "./RouteExamResult";
import RouteMaterial from "./RouteMaterial";
import SubjectList from "./SubjectList";
import TeacherProfile from "./Profile";
import NavBar from "./NavBar";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

function Teacher() {
  return (
    <div className="Teacher">
      <Router>
        <div className="Teacher">
          <NavBar />
          <Switch>
            <Route exact path="/teacher" component={TeacherProfile} />
            <Route exact path="/results" component={RouteExamResult} />
            <Route exact path="/materials" component={RouteMaterial} />
            <Route exact path="/subjects" component={SubjectList} />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default Teacher;
