import React from "react";
import "../../../node_modules/bootstrap/dist/css/bootstrap.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import AddStudent from "./ManageStudent/AddStudent";
import ViewStudent from "./ManageStudent/ViewStudent";
import EditStudent from "./ManageStudent/EditStudent";
import ListStudent from "./ManageStudent/ListStudent";

function RouteStudent() {
  return (
    <div className="App">
      <Router>
        <div className="App">
          <Switch>
            <Route exact path="/students" component={ListStudent} />
            <Route exact path="/students/add" component={AddStudent} />
            <Route exact path="/students/edit/:id" component={EditStudent} />
            <Route exact path="/students/:id" component={ViewStudent} />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default RouteStudent;
