import React from "react";
import "../../../node_modules/bootstrap/dist/css/bootstrap.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import AddSubject from "./ManageSubject/AddSubject";
import ViewSubject from "./ManageSubject/ViewSubject";
import EditSubject from "./ManageSubject/EditSubject";
import ListSubject from "./ManageSubject/ListSubject";

function RouteSubject() {
  return (
    <div className="App">
      <Router>
        <div className="App">
          <Switch>
            <Route exact path="/subjects" component={ListSubject} />
            <Route exact path="/subjects/add" component={AddSubject} />
            <Route exact path="/subjects/edit/:id" component={EditSubject} />
            <Route exact path="/subjects/:id" component={ViewSubject} />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default RouteSubject;
