import React from "react";
import "../../../node_modules/bootstrap/dist/css/bootstrap.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import AddTeacher from "./ManageTeacher/AddTeacher";
import ViewTeacher from "./ManageTeacher/ViewTeacher";
import EditTeacher from "./ManageTeacher/EditTeacher";
import ListTeacher from "./ManageTeacher/ListTeacher";

function RouteTeacher() {
  return (
    <div className="App">
      <Router>
        <div className="App">
          <Switch>
            <Route exact path="/teachers" component={ListTeacher} />
            <Route exact path="/teachers/add" component={AddTeacher} />
            <Route exact path="/teachers/edit/:id" component={EditTeacher} />
            <Route exact path="/teachers/:id" component={ViewTeacher} />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default RouteTeacher;
