import React, { useState } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";

const AddResult = () => {
  let history = useHistory();
  const [result, setResult] = useState({
    studentId: "",
    subjectId: "",
    examType: "",
    examDate: "",
    examInternalMark: "",
    examExternalMark: "",
    attendance: ""
  });

  const {
    studentId,
    subjectId,
    examType,
    examDate,
    examInternalMark,
    examExternalMark,
    attendance
  } = result ;
  const onInputChange = (e) => {
    setResult({ ...result, [e.target.name]: e.target.value });
  };

  const config = {
    headers:{
      Authorization : 'Bearer ' + localStorage.getItem('token')
    }
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    await axios.post("http://localhost:8080/exam", result, config);
    console.log(result);
    history.push(`/results/${result.studentId}`);
  };
  return (
    <div className="container">
      <div className="w-75 mx-auto shadow p-5">
        <h2 className="text-center mb-4">Add A Result</h2>
        <form onSubmit={(e) => onSubmit(e)}>
        <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter StudentId"
              name="studentId"
              value={studentId}
              onChange={(e) => onInputChange(e)}
            />
          </div>  
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter SubjectId"
              name="subjectId"
              value={subjectId}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter examType"
              name="examType"
              value={examType}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter examDate"
              name="examDate"
              value={examDate}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter InternalExam Mark"
              name="examInternalMark"
              value={examInternalMark}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter ExternalExam Mark"
              name="examExternalMark"
              value={examExternalMark}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control form-control-lg"
              placeholder="Enter attendance"
              name="attendance"
              value={attendance}
              onChange={(e) => onInputChange(e)}
            />
          </div>
          <button className="btn btn-primary btn-block">Add Result</button>
        </form>
      </div>
    </div>
  );
};

export default AddResult;
