import React from "react";
import "../../../node_modules/bootstrap/dist/css/bootstrap.css";
import ExamResult from "./ExamResult";
import ViewMaterial from "./ViewMaterial.js";
import SubjectList from "./Subject";
import StudentProfile from "./Profile";
import NavBar from "./NavBar";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

function Student() {
  return (
    <div className="Student">
      <Router>
        <div className="Student">
          <NavBar />
          <Switch>
            <Route exact path="/student" component={StudentProfile} />
            <Route exact path="/results" component={ExamResult} />
            <Route exact path="/materials/:id" component={ViewMaterial} />
            <Route exact path="/student/subjects" component={SubjectList} />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default Student;
