import React from "react";
import "../../../node_modules/bootstrap/dist/css/bootstrap.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import AddMaterial from "./ManageMaterial/AddMaterial";
import ViewMaterial from "./ManageMaterial/ViewMaterial";
import EditMaterial from "./ManageMaterial/EditMaterial";
import ListMaterial from "./ManageMaterial/ListMaterial";

function RouteMaterial() {
  return (
    <div className="App">
      <Router>
        <div className="App">
          <Switch>
            <Route exact path="/materials" component={ListMaterial} />
            <Route exact path="/materials/add" component={AddMaterial} />
            <Route exact path="/materials/edit/:id" component={EditMaterial} />
            <Route exact path="/materials/:id" component={ViewMaterial} />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default RouteMaterial;
