import React from "react";
import "../../../node_modules/bootstrap/dist/css/bootstrap.css";
import RouteStudent from "./RouteStudent";
import RouteTeacher from "./RouteTeacher";
import RouteSubject from "./RouteSubject";
import NavBar from "./ManageNavBar/NavBar";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

function Admin() {
  return (
    <div className="Admin">
      <Router>
        <div className="Admin">
          <NavBar />
          <Switch>
            <Route exact path="/students" component={RouteStudent} />
            <Route exact path="/teachers" component={RouteTeacher} />
            <Route exact path="/subjects" component={RouteSubject} />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default Admin;
