import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import VisibilityIcon from "@material-ui/icons/Visibility";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import PersonAddIcon from "@material-ui/icons/PersonAdd";
import SearchIcon from "@material-ui/icons/Search";
import { Input } from "@material-ui/core";

const ListTeacher = () => {
  const [teachers, setTeacher] = useState([]);
  const [q, setQ] = useState("");

  useEffect(() => {
    loadTeachers();
  }, []);

  const config = {
    headers: {
      Authorization: "Bearer " + localStorage.getItem("token"),
    },
  };

  const loadTeachers = async () => {
    const result = await axios.get(
      "http://localhost:8080/admin/teacher",
      config
    );
    console.log(result);
    setTeacher(result.data);
  };

  function search(teachers) {
    const columns = teachers[0] && Object.keys(teachers[0]);
    return teachers.filter((teacher) =>
      columns.some(
        (column) => teacher[column].toLowerCase().indexOf(q.toLowerCase()) > -1
      )
    );
  }

  const deleteTeacher = async (id) => {
    await axios.delete(`http://localhost:8080/admin/teacher/${id}`, config);
    loadTeachers();
  };

  return (
    <div className="container">
      <div className="py-4">
        <Link className="btn btn-outline-primary mb-2" to="/teachers/add">
          <PersonAddIcon />
        </Link>
        <SearchIcon />
        <Input
          type="text"
          placeholder="Search..."
          value={q}
          onChange={(e) => setQ(e.target.value)}
        />

        <table class="table border shadow">
          <thead class="thead-dark">
            <tr>
              <th scope="col">#</th>
              <th scope="col">TeacherId</th>
              <th scope="col">Teacher Name</th>
              <th scope="col">Email</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {search(teachers).map((teacher, index) => (
              <tr>
                <th scope="row">{index + 1}</th>
                <td>{teacher.teacherId}</td>
                <td>{teacher.teacherName}</td>
                <td>{teacher.teacherEmail}</td>
                <td>
                  <Link
                    class="btn btn-primary mr-2"
                    to={`/teachers/${teacher.teacherId}`}
                  >
                    <VisibilityIcon />
                  </Link>
                  <Link
                    class="btn btn-outline-primary mr-2"
                    to={`/teachers/edit/${teacher.teacherId}`}
                  >
                    <EditIcon />
                  </Link>
                  <Link
                    class="btn btn-danger"
                    onClick={() => deleteTeacher(teacher.teacherId)}
                  >
                    <DeleteIcon />
                  </Link>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default ListTeacher;
